import Vue from 'vue'
import VueRouter from 'vue-router'

import IndexAdmin from '@/components/admin/IndexAdmin'
import IndexPolitico from '@/components/politico/IndexPolitico'
import AdicionarPolitico from '@/components/politico/AdicionarPolitico'
import EditarPolitico from '@/components/politico/EditarPolitico'
import IndexReclamacao from '@/components/reclamacao/IndexReclamacao'
import AdicionarReclamacao from '@/components/reclamacao/AdicionarReclamacao'
import EditarReclamacao from '@/components/reclamacao/EditarReclamacao'
import VerReclamacao from '@/components/reclamacao/VerReclamacao'
import Sobre from '@/components/sobre/Sobre'
import Auth from '@/components/auth/Auth'

import { userKey } from '@/global'

Vue.use(VueRouter)

const routes =[{
    name: 'IndexAdmin',
    path: '/',
    component: IndexAdmin
},{
    name: 'IndexPolitico',
    path: '/politico/index',
    component: IndexPolitico  ,
    meta: { requiresAdmin: true }
},{
    name: 'AdicionarPolitico',
    path: '/politico/adicionar',
    component: AdicionarPolitico , 
    meta: { requiresAdmin: true }
}, {
    name: 'EditarPolitico',
    path: '/politico/:id/edit',
    component: EditarPolitico ,
    meta: { requiresAdmin: true }
},{
    name: 'IndexReclamacao',
    path: '/reclamacao/index',
    component: IndexReclamacao  
    
},{
    name: 'AdicionarReclamacao',
    path: '/reclamacao/adicionar',
    component: AdicionarReclamacao  
}, {
    name: 'EditarReclamacao',
    path: '/reclamacao/:id/edit',
    component: EditarReclamacao
}, {
    name: 'VerReclamacao',
    path: '/reclamacao/:id/show',
    component: VerReclamacao
}, {
    name: 'auth',
    path: '/auth',
    component: Auth
},
{
    name: 'sobre',
    path: '/sobre',
    component: Sobre
}

]




 const router = new VueRouter({
    mode: 'history',
    routes: routes
})

router.beforeEach((to, from, next) => {
    const json = localStorage.getItem(userKey)

    if(to.matched.some(record => record.meta.requiresAdmin)) {
        const user = JSON.parse(json)
        user && user.admin ? next() : next({ path: '/sobre' })
    } else {
        next()
    }
})

export default router