<template>
  <div>
    <h1>Politicos</h1>
    <router-link class="btn btn-primary mb-3" to="/politico/adicionar"> 
    Novo Político
    </router-link>
    <b-table  hover striped :items="users" :fields="fields">
      <template #cell(actions)="data">

          <b-button variant="warning" @click="loadUser(data.item)" class="mr-2">
              <i class="fa fa-pencil"></i>
          </b-button>

          <b-button variant="danger" @click="remove(data.item)">
              <i class="fa fa-trash"></i>
          </b-button>
      </template>
    </b-table>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from "vuex"
import {baseApiUrl, showError} from '@/global'
export default {
computed: mapState(['algo']),
  name: 'IndexPolitico',
  data: function(){
      return {
          mode: 'save',
          user: {},
          users: [],
          fields: [
          { key: 'id', label: 'Código', sortable: true },
          { key: 'name', label: 'Nome', sortable: true },
          { key: 'partido', label: 'Partido', sortable: true },
          { key: 'actions', label: 'Ações' }
          ]
      }
  },
  methods: {
        loadUsers(){
        const url = `${baseApiUrl}/politico/index`
        console.log(this.$store.state.algo)
        console.log("ola")
        //http://localhost:4005
        axios.get(url).then(res=>{
            console.log(res.data)
            this.users = res.data;
          
        }) 
    },
            remove(user) {
            const id = user.id
            axios.delete(`${baseApiUrl}/politico/delete/${id}`)
            
                .then(() => {
                    this.$toasted.global.defaultSuccess()
                    this.reset()
                })
                .catch(showError)
        }
    ,
        loadUser(user){
        this.$router.push({ name: 'EditarPolitico', params: { id: `${user.id}` } })
        
    },
        reset() {
            this.mode = 'save'
            this.user = {}
            this.loadUsers()
        },
  },
  mounted() { 
    this.loadUsers()
  }

}
</script>

