<template>
  <div>
    <h1>Editar Políticos</h1>
    <b-form>
        <input id="id" type="hidden" v-model="user.id" />
        <b-row>
            <b-col md="6" sm="12">
                <b-form-group label="Nome:" label-for="nome">
                    <b-form-input id="name" type="text"
                        v-model="user.name"  required
                        placeholder="Informe o Nome do Político..." />
                </b-form-group>
            </b-col>
            <b-col md="6" sm="12">
                <b-form-group label="Partido" label-for="partido">
                    <b-form-input id="partido" type="text"
                       v-model="user.partido"   required
                        
                        placeholder="Informe o partido do Político..." />
                </b-form-group>
            </b-col>
        </b-row>
    </b-form>
    <div class="row mt-3">
        <div class="col-md-10">
            <div id="dvAlert">
                <div class="alert alert-info">Preencha corretamente todos os campos</div>
            </div>
        </div>

        <div class="col-md-2 text-right">
            <div class="btn btn-success w-100" 
                @click="save">Salvar
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import {baseApiUrl, showError} from '@/global'
import axios from 'axios'
export default {
    name: 'EditarPolitico',
    data: function(){
        return {
            user: {}
        }
    },
    methods: {
        edit(){        
            let url = this.$route.params
            axios.get(`${baseApiUrl}/politico/${url.id}/edit`)
                .then(res => {this.user = res.data})
                .catch(showError)

                
    },
        reset() { 
        this.user = {};
        
    },
        save(){
            const id = this.user.id 
        axios['put'](`${baseApiUrl}/politico/update/${id}`,this.user)
        .then(() =>{
            this.$toasted.global.defaultSuccess()
            this.reset()
            this.$router.push({ name: 'IndexPolitico' })
        })
        .catch(showError)
    }
        
    },
    mounted() { 
        this.edit()
    }



}
</script>

