<template>
  <div>
    <h1>Editar Reclamação</h1>
    <b-form>
        <b-row>
            <b-col md="5" sm="12">
                <b-form-group label="Título da reclamação" label-for="titulo">
                    <b-form-input id="titulo" type="text"
                        v-model="posting.titulo"  required
                        placeholder="Informe o titulo da reclamação" />
                </b-form-group>
            </b-col>
            <b-col md="4" sm="12">
                <b-form-group label="área " label-for="area">
                    <b-form-input id="area" type="text"
                          required
                        v-model="posting.area"
                        placeholder="Informe a área da reclamação" />
                </b-form-group>
            </b-col>
            <b-col md="3" sm="12">
                <b-form-group  
                    label="Politico" label-for="politico_id">
                    <b-form-select id="politico_id"
                        :options="users"  v-model="posting.politico_id"
                          />
                </b-form-group>
            </b-col>
        </b-row>
        <b-row>
            <b-col md="12" sm="12">
                <b-form-group label="Descrição rápida" label-for="descricao">
                    <b-form-input id="descricao" type="text"
                        v-model="posting.descricao"  required
                        placeholder="Informe o descricao do Político..." />
                </b-form-group>
            </b-col>
            <b-col md="12" sm="12">
                <b-form-group label="Link de noticia" label-for="thumblink">
                    <b-form-input id="thumblink" type="text"
                        v-model="posting.thumblink"  required
                        
                        placeholder="Informe o thumblink do Político..." />
                </b-form-group>
            </b-col>
            <b-col md="12" sm="12">
                <b-form-group label="Link de noticia" label-for="texto">
                    <b-form-textarea
                        id="texto"
                        v-model="posting.texto"
                        placeholder="Enter something..."
                        rows="3"
                        max-rows="6"
                        ></b-form-textarea>
                </b-form-group>
            </b-col>
        </b-row>
    </b-form>
    <div class="row mt-3">
        <div class="col-md-10">
            <div id="dvAlert">
                <div class="alert alert-info">Preencha corretamente todos os campos</div>
            </div>
        </div>

        <div class="col-md-2 text-right">
            <div class="btn btn-success w-100" 
                @click="save">Salvar
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import {baseApiUrl, showError} from '@/global'
import axios from 'axios'
export default {
    name: 'EditarReclamacao',
    data: function(){
        return {
            posting: {},
            users: [
                
            ]
        }
    },
    methods: {
        edit(){        
            let url = this.$route.params
            axios.get(`${baseApiUrl}/Reclamacao/${url.id}/edit`)
                .then(res => {this.posting = res.data})
                .catch(showError)
                
    },
        reset() { 
        this.posting = {};
        
    },
        save(){
            const id = this.posting.id 
        axios['put'](`${baseApiUrl}/reclamacao/update/${id}`,this.posting)
        .then(() =>{
            this.$toasted.global.defaultSuccess()
            this.reset()
            
            this.$router.push({ name: 'IndexReclamacao' })
            
        })
        .catch(showError)
        
    },

        loadUsers() {
            const url = `${baseApiUrl}/reclamacao/adicionar`
            axios.get(url).then(res => {
                this.users = res.data.map(user => {
                    return { value: user.id, text: user.name }
                })
            })
    }
        
    },
    mounted() { 
        this.edit()
        this.loadUsers()
    }



}
</script>

