<template>
  <div>
    <h1>{{this.users2[0].titulo}}</h1>
    <h5>{{this.users2[0].area}}</h5>
    
    <router-link class="btn btn-primary mb-3" to="/reclamacao/editar"> 
   editar reclamação
    </router-link>



 <b-table  hover striped :items="users2" :fields="fields">

    </b-table>
    <hr/>
{{this.users2[0].texto}}
    
    
  </div>
</template>

<script>
import axios from 'axios'
import {baseApiUrl,showError } from '@/global'
export default {
  name: 'IndexReclamacao',
  data: function(){
      return {
          page: 1,
          limit: 0,
          count: 0,
          try: {},
          users2: [],
          fields: [
          { key: 'name', label: 'Nome', sortable: true },
          { key: 'partido', label: 'partido', sortable: true },
          { key: 'datap', label: 'Data', sortable: true },
            
          ]
      }
  },
  methods: {
        loadUsers(){
            let url = this.$route.params
            axios.get(`${baseApiUrl}/Reclamacao/${url.id}/show`)
                .then(res => {this.users2 = res.data} )
                .then(this.try = this.users2)
                .catch(showError)
        
    },
        loadVer(user){
        this.$router.push({ name: 'VerReclamacao', params: { id: `${user.id}` } })
        
    }
  },

  mounted() { 
    this.loadUsers()
  }

}
</script>

