<template>
  <div>
    <h1>Reclamações</h1>
    <router-link class="btn btn-primary mb-3" to="/reclamacao/adicionar"> 
    Nova reclamacao
    </router-link>
    <b-table  hover striped :items="users" :fields="fields">
      
      <template #cell(actions)="data">

          <b-button variant="warning" @click="loadEdit(data.item)" class="mr-2">
              <i class="fa fa-pencil"></i>
          </b-button>
          <b-button variant="success" @click="loadVer(data.item)" class="mr-2">
              <i class="fa fa-eye"></i>
          </b-button>
          <b-button variant="danger" @click="remove(data.item)">
              <i class="fa fa-trash"></i>
          </b-button>
      </template>
    </b-table>
    <b-pagination size="md" v-model="page" :total-rows="count" :per-page="limit" />
  </div>
</template>

<script>
import axios from 'axios'
import {baseApiUrl , showError} from '@/global'
export default {
  name: 'IndexReclamacao',
  data: function(){
      return {
          page: 1,
          limit: 0,
          count: 0,
          user: {},
          users: [],
          fields: [
          { key: 'id', label: 'Código', sortable: true },
          { key: 'titulo', label: 'Título', sortable: true },
          { key: 'area', label: 'Área', sortable: true },
          { key: 'name', label: 'Polílico', sortable: true },
          { key: 'datap', label: 'Publicado em:',formatter: 'fullName2', sortable: true },
          { key: 'actions', label: 'Ações' }
          ]
      }
  },
  methods: {
        loadUsers(){
            const url = `${baseApiUrl}/reclamacao/index?page=${this.page}`
            axios.get(url).then(res => {
              console.log(res.data)
                this.users = res.data.data
                this.count = res.data.count
                this.limit = res.data.limit
                
            })
},
        loadEdit(user){
        this.$router.push({ name: 'EditarReclamacao', params: { id: `${user.id}` } })
        
    },
      remove(user) {
        const id = user.id
        axios.delete(`${baseApiUrl}/reclamacao/delete/${id}`)
          .then(() => {
              this.$toasted.global.defaultSuccess()
              this.reset()
          })
          .catch(showError)
      },
        reset() {
            this.user = {}
            this.loadUsers()
        },
        loadVer(user){
        this.$router.push({ name: 'VerReclamacao', params: { id: `${user.id}` } })
        
    },
        fullName2(value) {
       let   dataSliced = value.slice(0, 10)
       let splitString = dataSliced.split("-");
       let reverseArray = splitString.reverse();
       //let joinArray = reverseArray.join("");

        return `${reverseArray[0]}/${reverseArray[1]}/${reverseArray[2]}`
      }
  },
  watch: {
    page() {
        this.loadUsers()
    }
    },
  mounted() { 
    this.loadUsers()
  }

}
</script>

